import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';
import { SoapService } from '../soap.service';
import { api_base_url, api_user, api_pass, api_p2b_url } from '../../config';
/**
 * Generated class for the SearchForwardPrpoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-search-forward-prpo',
  providers: [SoapService],
  templateUrl: 'search-forward-prpo.html',
})
export class SearchForwardPrpoPage {
  searchText: string = "";
  items: any = [];
  filterItems: any[];
  selectedItems: any[] = [];
  displayOk: any = false;


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public soapService: SoapService,
    public toastCtrl: ToastController
    ) {      
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchForwardPrpoPage');
  }

  getForward() {          
      this.soapService
        .post(api_base_url, 'eoffice_findemployee', {
          fStream: JSON.stringify(
            {
              usernameEDI: api_user,
              passwordEDI: api_pass,
              search: this.searchText.toUpperCase(),
              atas: 1,
              bawah: 5
            }
          )
        }).then(result => {
          var responData = JSON.parse(String(result));
          console.log(responData);
          if (responData['rcmsg'] == "SUCCESS") {
            this.items = responData['data'];     
            this.FilterItems();
          } else {
            let toast = this.toastCtrl.create({
              message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
              duration: 3000,
              position: 'bottom'
            });
            toast.present();                    
          }
        })
        .catch(error => {
          console.log(error);
          let toast = this.toastCtrl.create({
            message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();          
        });    
  }

  FilterItems() {
    this.filterItems = this.items;
    if (this.searchText.trim() !== '') {
      this.filterItems = this.filterItems.filter((item) => {
        return (item.NAMA.toLowerCase().indexOf(this.searchText.toLowerCase()) > -1);
      });
    }
  }

  CheckChange(item: any) {
    for (let index = 0; index < this.filterItems.length; index++) {
      const element = this.filterItems[index];
      if (element.NIPP == item.NIPP) {
        this.filterItems[index].selected = true;
        this.selectedItems = element;
      }
      else {
        this.filterItems[index].selected = false;
      }
    }
  }

  CloseModel() {
    this.viewCtrl.dismiss(this.selectedItems);
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

}



